var Vue = require('vue')
var VueRouter = require('vue-router')
var VueResource = require('vue-resource');

var Index = require('./Index.vue')
var Indent = require('./Indent.vue')
var Detail = require('./Detail.vue')

//css
require('./assets/css/index.css')

//token
var getQueryParam = function (paras) {
  var url = location.href;
  url = url.substring(0, url.indexOf('#'));  //#
  var paraString = url.substring(url.indexOf("?") + 1, url.length).split("&"); //?
  //paraStringtoken=abcde
  var paraObj = {}
  for (i = 0; j = paraString[i]; i++) {
    paraObj[j.substring(0, j.indexOf("=")).toLowerCase()] = j.substring(j.indexOf("=") + 1, j.length);
  }
  var returnValue = paraObj[paras.toLowerCase()];
  //returnValue"token="abcde
  if (typeof (returnValue) == "undefined") {
    return "";
  } else {
    return returnValue;
  }
}
var token = getQueryParam('token');
if (token) {
  localStorage.setItem('token', token);
}


//API 


Vue.use(VueRouter);
Vue.use(VueResource);
Vue.http.headers.common['Content-Type'] = 'application/json';
localStorage.setItem('base_url', 'http://api-test.shangcars.com');

const router = new VueRouter({
  //mode: 'history',
  base: __dirname,
  routes: [
    {path: '/', component: Index },
    {path: '/indent', component: Indent },
    {name:'detail', path: '/detail/:id', component: Detail}
  ]
});


new Vue({
  router: router,
  props: ['order_success']
}).$mount('#app');





