<template>
	
    <div>
        <header class="header_title">
            <!--  -->
            <div  v-if="$route.params.order_success_tips" class="OverfullDebt success-tips" id="Indent_OverfullDebt">
                <img src="./assets/img/Order_success.gif"/>
                <p class="Order_Success_Text"></p>
            </div>
            <div  v-if="$route.params.order_success_tips" class="shade success-tips" id="Indent_shade"></div>
            <!--  End-->

            <span></span>
        </header>

        <section style="margin-bottom: 49px;">
            <div v-for="item in billings">
                <div class="bill">
                    <p>  <span>{{item.total_amount}}</span></p><button v-if="!item.is_paied"><a href="/billings/pay"></a></button>
                </div>

                <div class="indent_content" v-for="order in item.order_list">
                    <div class="box_one"><span class="box_one_left"></span><span class="box_one_right num_size_hack">{{order.delivery_date}}</span></div>
                    <div class="box_one"><span class="box_one_left"> &nbsp; &nbsp;</span><span class="box_one_right num_size_hack">{{order.total_amount}}</span><span class="box_one_right"></span></div>
                    <div id="Go_indent_details">
                        <router-link :to="{name:'detail', params:{id:order.order_id}}">
                            <div class="indent_right" v-if="!order.is_paied"></div>
                            <div class="indent_right" v-if="order.is_paied"></div>
                            <img src="./assets/img/skip.png" class="right_button"/>
                        </router-link>
                    </div>
                    <div class="border_bottom"></div>
                </div>
            </div>




            <!--.-->
            <!--<div class="indent_content">
                <div class="box_one"><span class="box_one_left"></span><span class="box_one_right">2016-11-04</span></div>
                <div class="box_one"><span class="box_one_left"> &nbsp; &nbsp;</span><span class="box_one_right">11.58</span><span class="box_one_right"></span></div>
                <div id="Go_indent_details">
                    <a>
                        <div class="indent_right"></div>
                        <div class="indent_right" style="display: none;"></div>
                        <img src="./assets/img/skip.png" class="right_button"/>
                    </a>
                </div>
                <div class="border_bottom"></div>
            </div>-->

        </section>
        <footer>
            <!--  -->
            <div class="index_bottom">
            	
                <div class="index_bottom_left">
                    <dl>
						<router-link to="/">
	                        <dt>
	                            <img src="./assets/img/cart.gif"/>
	                        </dt>
	                        <dd><p></p></dd>
						</router-link>
                    </dl>
                </div>
            	
                <div class="cut-off"></div>
                <div class="index_bottom_right">
                    <dl>
                        <dt>
                            <img src="./assets/img/bill_active.gif"/>
                        </dt>
                        <dd><p style="color:#52BE44;"></p></dd>
                    </dl>
                </div>
            </div>
        </footer>
    </div>
</template>
<script>
module.exports = {
    data: function () {
        return {
            billings: []
        }
    },
    created: function () {
        this.fetchData();
        setTimeout(function(){
            var nodes = document.getElementsByClassName('success-tips');
            for (i in nodes) {
                node = nodes[i];
                if (node.style) {
                    node.style.display = 'none';
                }
            }
        }, 3000)
    },
    methods: {
        fetchData: function () {
            this.$http.get(localStorage.getItem('base_url') + '/billings?token='+localStorage.token).then(function (res) {
                if (res.body.err_code == 0) {
                    this.billings = res.body.billings;
                }
            }, function (res) {});
        }
    }
}
</script>



