<template>
    <div>
        <header class="header_title">

            <img src="./assets/img/left.png" v-on:click="detail_back" /><span></span>

        </header>
        <section>
            <div class="details">
                <p><span class="num_size_hack">{{order.order_id}}</span></p>
                <p><span class="num_size_hack">{{order.created_at}}</span></p>
                <p><span class="num_size_hack">{{order.delivery_date}}</span></p>
                <p>  <span class="num_size_hack">{{order.total_amount}}</span><span></span></p>
                <p><span v-if="order.is_paied"></span><span v-if="!order.is_paied"></span></p>
            </div>

            <div class="indent_bottom" v-for="item in order.items">
                <p>{{item.name}}<span class="indent_bottom_right">{{item.quantity}}</span></p>
                <p><span>{{item.unit}}</span>
                    <span class="indent_bottom_right">
							  		<span>&yen;</span><span>{{item.price*100*item.quantity/100}}</span>
					</span>
                </p>
                <div class="border_bottom"></div>
            </div>
            <!--.-->
            <!--<div class="indent_bottom">
                <p><span class="indent_bottom_right">X2</span></p>
                <p><span style="margin-left: 10px;"></span>
                              <span class="indent_bottom_right">
                                      <span>&yen;</span><span>19.8</span>
                              </span>
                </p>
                <div class="border_bottom"></div>
            </div>-->



        </section>
        <footer>

        </footer>
    </div>
</template>

<script>
module.exports = {
    data: function () {
        return {
            order: {}
        }
    },
    created: function () {
        this.fetchData();
    },
    methods: {
        fetchData: function (){
            this.$http.get(localStorage.getItem('base_url') + '/order/'+this.$route.params.id+'?token='+localStorage.token).then(function (res) {
                if (res.body.err_code == 0) {
                    this.order = res.body;
                }
            }, function (res) {});
        },
        detail_back: function(){
        	window.history.go(-1);
        }
    }
}
</script>


